  /**
 * @file
 * Layout settings.
 */

// Susy settings.
// Check out more info: http://susy.readthedocs.org/en/latest/settings/
$susy: (
  math: fluid,
  container-position: center,
  columns: 12,
  gutters: .25,
  global-box-sizing: border-box,
);

/* Sticky footer */

body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

#main {
  flex: 1 0 auto;
  width: 100%;
}
